
.cart-control {
  font-size: 0;
}
.cart-control .cart-decrease,
.cart-control .cart-add {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  padding: 6px;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.cart-control .cart-count {
  display: inline-block;
  font-size: 10px;
  width: 12px;
  text-align: center;
  line-height: 24px;
  padding-top: 6px;
  color: #93999f;
  vertical-align: top;
}
.cart-control .cart-decrease.move-enter-active,
.cart-control .cart-decrease.move-leave {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3D(0, 0, 0) rotate(180deg);
          transform: translate3D(0, 0, 0) rotate(180deg);
  opacity: 1;
}
.cart-control .cart-decrease.move-enter,
.cart-control .cart-decrease.move-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3D(24px, 0, 0) rotate(0deg);
          transform: translate3D(24px, 0, 0) rotate(0deg);
  opacity: 0;
}
.cart-control .cart-decrease.move-leave-active {
  -webkit-transform: translate3D(24px, 0, 0) rotate(-180deg);
          transform: translate3D(24px, 0, 0) rotate(-180deg);
}


