
.tab {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:after {
  display: block;
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.tab-item {
  -webkit-flex: 1;
  text-align: center;
}
.tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.tab-item > a.active {
  color: #f01414;
}


